.body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  background-color: #fff;
  color: #424242;
  max-width: 960px;
  padding: 16px;
}

.container {
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
}
.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.info {
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
}
.info-result {
  display: block;
}
.info-title {
}
.image-tube {
  width: 400px;
  height: 300px;
  padding: 0px;
  overflow: hidden;
}
.photo-card {
  padding-right: 30px;
}
.images {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.button.is-hidden {
  display: none;
}
.search-form {
  margin-top: 40px;
  margin-bottom: 20px;
}
